.header {
  border-bottom: 1px solid #d4d4d4;

  .header_nav {
  }

  .header_contact_info {
    background: #f9fafd;
    width: 100%;
    line-height: 2em;
    padding: 0.2em 3em 0 0.2em;
  
    ul {
      list-style: none;
      display: grid;
      grid-auto-flow: column;
      grid-gap: 1em;
      justify-content: end;
  
      li a {
        color: #000;
        text-decoration: none;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    border: none;

    .header_contact_info ul {
      display: grid;
      grid-template-rows: 1fr 1fr;
      grid-gap: 0;

      li {
        padding-bottom: .4em;
        text-align: end;
      }
    }
  }
  
}