@mixin port_item_before {
  position: absolute;
  bottom: 0;
  line-height: 2.5em;
  background: #ffffffc0;
  width: 100%;
}

$blue: #2355da;

*,
body {
  margin: 0;
  padding: 0;
}
.home_wrapper {
  overflow-x: hidden;

  .home_header {
    padding: 2em 1em;
    display: flex;

    img {
      width: 90%;
      margin-left: 5%;
    }

    h2 {
      font-size: 3em;
      margin-top: 30%;
      line-height: 1.5em;
    }
  }
  @media screen and (max-width: 768px) {
    .home_header {
      .heading_block {
        order: 2;
      }

      h2 {
        margin: 1em;
        font-size: 2em;
      }
    }
  }
  // =========================
  .we_love_we_help_block {
    display: grid;
    text-align: center;
    font-size: 1.1em;

    .we_love_block {
      background: #f9fafd;
      padding: 4em 2em;

      p {
        margin-bottom: 4em;
      }

      p a {
        color: $blue;
        font-weight: 500;
      }

      .about_us_button {
        background: $blue;
        color: #fff;
        border-radius: 2em;
        border: 2px solid $blue;
        padding: 0.5em 2.5em;
        text-decoration: none;
        font-size: 1.1em;

        :hover {
          background: #fff;
          color: $blue;
        }
      }

      h2 {
        font-size: 3em;
        font-weight: 400;
        line-height: 1.5em;

        b {
          font-weight: 700;
        }
      }
    }
    .we_help_block {
      background: $blue;
      color: #fff;
      padding: 4em 2em;

      h2 {
        margin-bottom: 1em;
        font-weight: bold;
        font-size: 2.5em;
      }
    }
  }
  // ============================
  .our_services_wrap {
    padding: 4em 2em;

    ul {
      margin-top: 2em;
      list-style: none;
    }

    h2 {
      font-weight: 700;
    }

    li {
      div {
        img {
          width: 5em;
        }

        h3 {
          font-weight: 400;
          width: 60%;
          margin: 1em auto;
        }

        p {
          line-height: 1.9em;
          font-size: 1.3em;
        }
      }
    }
  }
  @media screen and (min-width: 769px) {
    .our_services_wrap {
      ul {
        li {
          div {
            display: grid;
            grid-auto-flow: column;
            grid-gap: 1em;
            justify-content: start;

            img {
              width: 4em;
            }

            h3 {
              text-align: start;
              width: 80%;
              margin: 0 0 0.7em;
            }
          }
          p {
            padding-left: 5em;
            text-align: start;
            font-size: 1.07em;
          }
        }
      }
    }
  }
  // ============================
  // .who_we_worked_with_wrapper {
  //   background: #f9fafd;
  //   padding: 2em 1em 4em 1em;

  //   h2 {
