  // ====================
  .portflio_wrapper {
    padding: 3.5em 0;

    ul {
      list-style: none;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr;
      grid-gap: 0.3em;
      justify-content: center;
      margin-bottom: 3em;

      li {
        position: relative;

        &:first-child:hover::before {
          content: "IG-Cleaner inner";
          @include port_item_before;
        }
        &:nth-child(2):hover::before {
          content: "Life Listr inner";
          @include port_item_before;
        }
        &:nth-child(3):hover::before {
          content: "Nomadic Wall inner";
          @include port_item_before;
        }
        &:nth-child(4):hover::before {
          content: "GoTeach inner";
          @include port_item_before;
        }
        &:nth-child(5):hover::before {
          content: "Keezi Keyboards Free inner";
          @include port_item_before;
        }
        &:nth-child(6):hover::before {
          content: "hidephoto inner";
          @include port_item_before;
        }

        img {
          width: 100%;
        }
      }
    }

    h2 {
      margin-bottom: 0.7em;
    }

    .check_out_button {
      text-decoration: none;
      background: $blue;
      color: #fff;
      border-radius: 2em;
      padding: 0.4em 2em 0.6em;
      font-size: 1.2em;

      &:hover {
        transition: 0.5s;
        background: #fff;
        color: #ff9100;
        border: 1px solid #ff9100;
      }
    }
  }
