  // =====================
  .client_comment_wrap {
    background: $blue;
    padding: 4em 1em;

    h2 {
      margin: 0 0 0.5em;
      font-weight: 700;
      color: #fff;
    }

    .commnets_slider_wrap {
      background: #fff;
      display: grid;
      justify-content: center;
      margin: 0 0.9em;
      padding: 1em 0.5em;

      div {
        line-height: 2em;

        img {
          margin: 2em auto;
          width: 7em;
          border-radius: 50%;
        }

        i {
          font-size: 2em;
          color: #c4c4c4;
          font-style: normal;
          line-height: 0.1em;
        }

        p {
          font-style: italic;
          font-size: 1.1em;
        }

        h5 {
          font-size: 1.1em;
        }

        span {
          font-size: 0.9em;
        }
      }
    }
  }
  // =======================
  .features_wrap {
    padding: 4em 1em;

    h2 {
      font-weight: 700;
      margin-bottom: 1em;
    }

    .subheading {
    }

    .features_list {
      padding: 1em 2.5em;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-gap: 1em;
      list-style: none;
      margin-top: 3em;

      li {
        div {
          display: grid;
          grid-auto-flow: column;
          grid-gap: 2em;
          justify-content: start;

          img {
            width: 5em;
          }

          h3 {
            font-weight: 600;
            text-align: start;
            width: 60%;
          }
        }

        p {
          margin-left: 7em;
          text-align: start;
        }
      }
    }
    @media screen and (max-width: 768px) {
      .features_list {
        padding: 0 1em;
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 2em;

        li {
          div {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 3em;
            justify-items: center;

            h3 {
              width: 100%;
              font-size: 1.5em;
              text-align: center;
            }

            img {
              width: 4.5em;
            }
          }

          p {
            margin-left: 0;
            text-align: center;
          }
        }
      }
    }
  }
  // =========================
  .technologies_wrap {
    padding: 2em 1em 4em 1em;
    background: #f9fafd;

    h2 {
      font-weight: 700;
      margin: 2em 0;
    }

    .slide {
      margin: 0 auto;
      width: 18em;
    }
  }
  // =====================
  .industries_wrap {
    padding: 3em 1em;

    h2 {
      margin: 1em 0;
    }

    .industries_list {
      padding-top: 2em;
      display: grid;
      grid-gap: 1em;

      div {
        background: #f9fafd;
        padding: 2em;

        h3 {
          font-size: 1.5em;
          margin-top: 1em;
        }

        &:hover {
          background: #d1d1d1;
          transition: 0.5s;
        }
      }
    }
  }
  @media screen and (min-width: 769px) {
    .industries_wrap {
      h2 {
        font-size: 3em;
        font-weight: 700;
      }

      .subheading {
        margin-top: -1em;
      }

      .industries_list {
        margin-top: 1em;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: 1fr 1fr;
      }
    }
  }
  // ==========================
  .our_values_wrap {
    margin-top: 2em;
    padding: 4em 1em;
    background: #f9fafd;

    h2 {
      font-weight: 700;
      margin: 1em 0;
    }

    .subheading {
      font-size: 0.9em;
    }

    .values_list {
      margin-top: 3em;
      display: grid;
      grid-auto-flow: row;
      grid-gap: 2em;
      list-style: none;

      li {
        h3 {
          margin: 1em 0;
          font-weight: 700;
        }
      }
    }
  }
  @media screen and (min-width: 769px) {
    .our_values_wrap {
      h2 {
        font-size: 3em;
        margin: 1em 0 0.5em 0;
      }

      .values_list {
        display: flex;
      }
    }
  }
