  // =============
  .form_wrap {
    background: #f9fafd;
    padding: 3em 1em;

    input,
    textarea {
      margin-bottom: 1em;
      height: 3em;
      border: 1px solid #dbdbdb;
    }
    textarea {
      padding-top: 0.7em;
    }
    input[type="submit"] {
      border-radius: 2em;
      border: 1px solid #3f3fff;
      color: #3f3fff;
      background: none;
      padding: 0 3em;
      font-weight: 500;

      &:hover {
        transition: 0.5s;
        background: $blue;
        color: #fff;
      }
    }

    .small_inputs_group {
      padding: 0 1em;
    }

    .big_inputs_group {
      padding: 0 1em;
    }
  }
  @media screen and (min-width: 769px) {
    .form_wrap {
      h3 {
        font-size: 2.3em;
        font-weight: 700;
      }
      .small_inputs_group {
        input {
          width: 35%;
        }
      }
    }
  }
  // =============
  .reach_us_wrap {
    background: $blue;
    padding: 3em 2em;

    h3 {
      font-weight: 700;
    }

    .location {
      color: #fff;

      .contact_buttons {
        display: grid;
        grid-auto-flow: column;
        grid-gap: 2em;
        justify-content: center;
        line-height: 3.5em;
        margin-top: 2em;
        font-weight: 500;

        .whatsapp_us {
          border: 2px solid #fff;
          padding: 0 2em;
          border-radius: 3em;
        }

        .call_us {
          border: 2px solid #fff;
          padding: 0 2em;
          border-radius: 3em;
        }
      }

      a {
        text-decoration: none;
        color: #fff;
      }

      .block {
        display: grid;
        grid-auto-flow: column;
        justify-content: start;
        line-height: 2em;
        margin-top: 2em;
        text-align: start;

        h4 {
          margin: 0.5em 1em;
        }

        .img_holder {
          padding-right: 1em;
        }

        .img {
          transform: scale(0.8);
          padding: 0 2em;
        }
        .img_second {
          transform: scale(0.8);
          padding: 0 0.5em 0 1em;
        }
      }
    }
  }
  @media screen and (max-width: 768px) {
    .reach_us_wrap {
      display: grid;
      grid-gap: 4em;
      padding: 3em 0;

      .location {
        .contact_buttons {
          display: grid;
          grid-auto-flow: column;
          grid-gap: 1em;
          // justify-content: center;
          // justify-items: center;
          // text-align: center;

          .whatsapp_us {
            white-space: nowrap;
          }

          .call_us {
            white-space: nowrap;
          }
        }

        .block {
          display: grid;
          grid-auto-flow: row;
          justify-content: center;
          text-align: center;

          .img {
            width: 15em;
            padding-bottom: 2em;
          }
        }
      }
    }
  }
}
